
































	import Event from '../../common/event.js'; 
	import mTab from "@/components/m-tab.vue";
	import mOrderList from "@/components/m-order-list.vue";
	import mNeedLogin from "@/components/m-need-login.vue";
	import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
	var page = 1,totalpage=1;
	export default {
		name:"m-footer-car",
		props:{},
		components:{
			mTab,
			uniLoadMore,
			mOrderList,
			mNeedLogin
		},
		data(){
			return{
				isLogin:false,
				tabActive:1,
				tabList:[
					{
						label:"待取货",
						id:1,
					},
					{
						label:"待支付",
						id:2,
					},
					{
						label:"待评价",
						id:3,
					},
					{
						label:"全部",
						id:4,
					},
				],
				orderList:[],
				mloading:'more'
			}
		},
		methods:{
			
			 // 付款
			payGood(res){
				let orderid = res.data.order.id;
				uni.navigateTo({
					url:"/pages/order/order?orderid="+orderid
				})
			},
			// 评论
			commentGood(res){
				// console.log('评论')	
				 let orderid = res.data.order.id;
				uni.navigateTo({
					url:"/pages/order/comment?orderid="+orderid
				})
			},
			 // 取货
			takeGood(res){
				let orderid = res.data.order.id;
				uni.navigateTo({
					url:"/pages/order/order?orderid="+orderid
				})
			},
			// 再来一单
// 			againGood(data){
// 				console.log('再来一单')	 	 
// 			},
			
			//是否登录了
			checkLogin(){
				let _this = this;
				_this.globelIsLogin().then(res=>{
					if(res=='success'){
						//已登录
						_this.isLogin=true;
					}
				}).catch(err=>{
					_this.isLogin=false
				});
			},
			// 获取订单
			getOrders(){
				let _this = this;
				uni.showLoading({});
				if(totalpage&&page > totalpage){
					uni.showToast({"title":"已经加载全部", icon:"none"});
					return ;
				}
				this.mPost('/server/o/myOrders',{
					state:_this.tabActive,
					start:page,
					length:20
				}).then(res=>{
					if(res.data){
						let data = res.data;
						if(data.orders){
							totalpage=data.pages|| 1;
							var newsList = data.orders;
							this.orderList = this.orderList.concat(newsList);
							uni.hideLoading();
							page++;
						}
					}
				}).catch(err=>{
					uni.hideLoading();
				});
			},
			// tab栏点击
			tabChange(item){
				this.tabActive= item.id;
				page = 1;
				this.orderList = [];
				this.getOrders();
			}
		},
		// 重置分页及数据
		onPullDownRefresh(){
			page = 1;
			this.orderList = [];
			this.getOrders();
		},
		// 加载更多
		onReachBottom(){
			this.mloading='loading';
			this.getOrders();
		},
		onLoad(option){
			this.tabActive=uni.getStorageSync('orderTab')||1;
			this.checkLogin();
			//获取订单
			page = 1;
			orderList:[];
			this.getOrders();
			uni.setStorageSync('orderTab', 1);
		}
// 		onShow(){
// 			this.tabActive=uni.getStorageSync('orderTab')||1;
// 			uni.setStorageSync('orderTab', 1);
// 			//获取订单
// 			page = 1;
// 			orderList:[];
// 			this.getOrders();
// 		}
	}
