


































































	export default {
		name:"m-product-list",
		props:{
			rowData:{
				type:Object,
				 // 对象或数组默认值必须从一个工厂函数获取
				default: function () {
					return { 
						
					}
				}
			},
			status:{
				type:[String,Number],
				default:""
			},
			title:{
				type:[String,Number],
				default:""
			},
			img:{
				type:[String,Number],
				default:""
			},
			proname:{
				type:[String,Number],
				default:""
			},
			extrctime:{ // 取货时间
				type:[String,Number],
				default:""
			},
			aboutPickingTime:{ // 取货时间
				type:[String,Number],
				default:""
			},
			describe:{
				type:[String,Number],
				default:""
			},
			price:{
				type:[String,Number],
				default:""
			},
			num:{
				type:[String,Number],
				default:""
			}
		},
		data() {
			return {
				
			};
		},
		methods:{
		 touchOnGoods(e){
			 this.$emit("touchOnGoods",{
				 data:this.rowData,
				 elem : e,
			 })
		 },
		 // 取货
		 takeGood(){
			this.$emit('takeGood',{data:this.rowData})
		 },
		  // 付款
		 payGood(){
		 	this.$emit('payGood',{data:this.rowData})	 
		 },
		 // 再来一单
// 		 againGood(){
// 		 	this.$emit('againGood',{data:this.rowData})	 	 
// 		 },
		 // 评论
		 commentGood(){
		 	this.$emit('commentGood',{data:this.rowData})	 	 
		 }
		}
	}
